<template>
    <div class="right-content right-content-expand">
        <div class="col-md-12 p-0">
            <div class="tab-content">
                <loader :is-visible="isLoading"></loader>
                <section class="page-action action-margin">
                    <div class="container-fluid p-0">
                        <div class="col-md-12 p-0">
                            <div class="theme-primary partition p-actions-expand tablehead isExtra">
                                <span class="p-name text-white" id="divModuleHeaderName">Add User</span>
                                <span class="p-actions float-right">
                                    <span class="d-none" id="reviewActions">
                                        <a id="addnewreview" href="javascript:;" title="" class="p-action-btn text-white"
                                            data-toggle-tooltip="tooltip" data-original-title="Add Review"><i
                                                class="fa fa-plus"></i><br><span>Add Review</span></a>
                                        <a class="p-action-btn text-white iconenable disabled" href="javascript:void();"
                                            onclick="deleteReview()" title="" id="DeleteMultiple_New"
                                            data-toggle-tooltip="tooltip"
                                            data-original-title="Please select one/multiple check box(s) to remove review(s)">
                                            <i class="fa fa-trash"></i><br><span>Remove</span>
                                        </a>
                                    </span>
                                    <span class="d-none" id="notesActions">
                                        <a checkprivilege="Yes" id="addnewNote" title="" class="p-action-btn text-white"
                                            data-toggle-tooltip="tooltip" data-original-title="Add Notes"
                                            href="/Note/Add?source=user_profile"><i class="fa fa-plus"></i><br><span>Add
                                                Notes</span></a>
                                        <a checkprivilege="Yes" href="javascript:deleteNoteMultiple();" id="DeleteMultiple"
                                            title="" class="p-action-btn text-white table-head-btn iconenable disabled"
                                            data-toggle-tooltip="tooltip"
                                            data-original-title="Please select one/multiple check box(s) to remove note(s)"><i
                                                class="fa fa-trash"></i><br><span>Remove Notes</span></a>
                                    </span>
                                    <span class="d-none" id="goalActions">
                                        <a checkprivilege="Yes" id="addnewreview" title="" class="p-action-btn text-white"
                                            data-toggle-tooltip="tooltip" data-original-title="Add a Goal"
                                            href="/Goal/Add?source=user_profile"><i class="fa fa-plus"></i><br><span>Add a
                                                Goal</span></a>
                                        <a checkprivilege="Yes" href="javascript:DeleteMultipleGoal();"
                                            id="DeleteMultipleGoal" title=""
                                            class="p-action-btn text-white iconenable disabled"
                                            data-toggle-tooltip="tooltip"
                                            data-original-title="Please select all that apply to remove goal(s)"><i
                                                class="fa fa-trash"></i><br><span>Remove Goal</span></a>
                                    </span>
                                    <span class="d-none" id="feedbackActions">
                                        <a id="addnewFeedback" class="p-action-btn text-white" data-toggle-tooltip="tooltip"
                                            data-original-title="Create" href="/Feedback/Add?source=user_profile"><i
                                                class="fa fa-plus"></i><br><span>Add Feedback</span></a>
                                        <a class="p-action-btn text-white iconenable disabled"
                                            href="javascript:deleteFeedback();"
                                            onclick="javascript:window.onbeforeunload=null;" title="" id="DeleteMultiple1"
                                            data-toggle-tooltip="tooltip"
                                            data-original-title="Please select one/multiple check box(s) to remove feedback(s)">
                                            <i class="fa fa-trash"></i><br><span>Remove</span>
                                        </a>
                                    </span>
                                    <span class="d-none" id="skillActions">
                                        <a checkprivilege="Yes" class="p-action-btn text-white" id="addSkills"
                                            source="user_profile" title="" data-toggle-tooltip="tooltip"
                                            data-original-title="Add Skill"
                                            href="/SkillAssignment/Manage?source=user_profile"><i
                                                class="fa fa-plus"></i><br><span>Add Skill</span>
                                        </a>
                                        <a checkprivilege="Yes" href="javascript:DeleteMultipleAssignedSkill();"
                                            id="DeleteMultipleSkill" title=""
                                            class="p-action-btn text-white iconenable disabled"
                                            data-toggle-tooltip="tooltip"
                                            data-original-title="Please select one/multiple checkbox(s) to remove skill(s)"><i
                                                class="fa fa-trash"></i><br><span>Remove Skill</span>
                                        </a>
                                    </span>
                                </span>
                            </div>
                        </div>
                    </div>
                </section>
                <dynamic-form lang="en" ref="refAddUser" :buttons="false" :schema="FormSchema"></dynamic-form>
                <div class="col-12 pt-2" id="accordionEx1" v-if="ShowAdvanceSetting">
                    <div class="card mb-3">
                        <div class="card-header DefaultTab">
                            <div class="card-link" role="tab" id="MainFunction">
                                <div class="mb-0 conversation-date heading-border"><a data-toggle="collapse"
                                        data-parent="#accordionEx1" href="#FucntionCollapse" aria-expanded="false"
                                        aria-controls="FucntionCollapse" class=""><span
                                            class="">{{ $t('AdvanceSettings') }}</span><em class="fa fa-angle-down rotate-icon pr-1"
                                            aria-hidden="true"></em></a>
                                </div>
                            </div>
                            <div id="FucntionCollapse" class="collapse multi-collapse show" role="tabpanel"
                                aria-labelledby="MainFunction" data-parent="#accordionEx1">
                                <div class="card-body p-0 customScrollForLeftBar llt" style="max-height:500px;">
                                    <div class="form-group">
                                        <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix"
                                            style="max-height: 214px; overflow: auto; display: block;">
                                        </ul>
                                    </div>
                                    <dynamic-form lang="en" ref="refAddUserAdvanceSetting" :buttons="false" :schema="FormSchemaAdvanceSettings">
                                    </dynamic-form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row flex-row-reverse mb-2">
                    <div class="col-lg-6 text-right">
                        <a class="btn btn-success formbtn" value="Save" title="Save" name="btnSave" v-on:click="SubmitUser"><i class="fa fa-save pr-2" aria-hidden="true"></i> Save</a>
                            <a title="" class="btn btn-danger formbtn widthhalf" href="/User/Index" data-toggle-tooltip="tooltip" data-original-title="Cancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                    </div>
                    <div class="col-lg-6">
                        <div class="py-2"><span class="mandatory">Fields marked with an asterisk (*) are mandatory.</span></div>
                    </div>
                </div>
            </div>
        </div>
        <AddCompanySetup :recordId="recordId" :title="getTitle" v-if="ShowAllData"  v-on:close="ShowAllData=false;recordId=0" v-bind:moduleName="getModuleName" v-bind:emitName="'addUserRecord'" v-bind:addApiUrl="getController" v-bind:subModule="getSubmoduleName" />
    </div>
</template>

<script>
import DataService from '../../services/DataService';
import AddCompanySetup from '@/components/Modals/AddCompanySetup.vue'
export default {
    components:{
        AddCompanySetup
    },
    data() {
        return {
            isLoading:false,
            FormSchema: [],
            FormSchemaAdvanceSettings:[],
            ShowAdvanceSetting:false,
            getTitle:'',
            getModuleName:'',
            getSubmoduleName:'',
            ShowAllData:false,
            getFormSchemaData:[],
            getFormSchemaAdvanceSettings:[],
            fileData:[]
        }
    },
    async created(){
        var vm = this;
        await vm.GetForm();
    },
    methods: {
        AddNewTitlePopUp(){
            var vm = this;
            vm.getTitle = vm.$t('AddUserTitle');
            vm.getModuleName = 'User';
            vm.getSubmoduleName = 'UserTitle';
            vm.getController = '/UserTitle/add';
            vm.ShowAllData = true;
        },
        AddADepartmentPopUp(){
            var vm = this;
            vm.getTitle = vm.$t('AddADepartment');
            vm.getModuleName = 'Department';
            vm.getSubmoduleName = 'Department';
            vm.getController = '/Department/Add';
            vm.ShowAllData = true;
        },
        AddDesignationPopUp(){
            var vm = this;
            vm.getTitle = vm.$t('AddDesignation');
            vm.getModuleName = 'Designation';
            vm.getSubmoduleName = 'Designation';
            vm.getController = '/Designation/Add';
            vm.ShowAllData = true;
        },
        AddShiftPopup(){
            var vm = this;
            vm.getTitle = vm.$t('Shift');
            vm.getModuleName = 'Shift';
            vm.getSubmoduleName = 'Shift';
            vm.getController = '/Shift/Add';
            vm.ShowAllData = true;
        },
        ValidateConfirmPassword(){
            var vm = this;
            debugger
            if(vm.FormSchema[0].Data[8].value != ''){
                if(vm.FormSchema[0].Data[8].value == vm.FormSchema[0].Data[9].value){
                    vm.FormSchema[0].Data[9].validationRules = {
                        "confirmed": false
                    }
                }else{
                    vm.FormSchema[0].Data[9].validationRules = {
                        "confirmed": true
                    }
                }
            }
        },
        async GetForm(){
            var vm = this;
            vm.isLoading = true;
            var params = `recordId=0&moduleName=User&subModuleCode=Cmp_User`;
            DataService.GetDynamicForm(params).then(response=>{
                debugger
                if(response.data.data.length > 0){
                    var getSchemaValue = vm.$refs.refAddUser.ConvertCustomFieldIntoDynamicFormObj(response.data.data);
                    vm.FormSchema.push(getSchemaValue[0]);
                    vm.FormSchema[0].Data.forEach(item=>{
                        if(item.name == "email_id"){
                            item.validationRules = {
                                "email" : true
                            }
                        }
                        else if(item.name == 'gender'){
                            item.config.options = [{name:'Male',value:'M'},{name:'Female',value:'F'}];
                        }else if(item.name == 'user_title_id'){
                            item.config.showAddIcon = true;
                            item.config.onAddButtonClick = vm.AddNewTitlePopUp;
                        }else if (item.name == "confirm_password"){
                            item.config.onKeyUp = vm.ValidateConfirmPassword;
                            item.validationRules = {
                                "confirmed" : false
                            }
                        }
                    })
                    vm.FormSchemaAdvanceSettings.push(getSchemaValue[1]);
                    vm.FormSchemaAdvanceSettings[0].Data.forEach(FSitem=>{
                        if(FSitem.name == 'shift_id'){
                            FSitem.config.showAddIcon = true;
                            FSitem.config.onAddButtonClick = vm.AddShiftPopup;
                        }else if(FSitem.name == 'department_id'){
                            FSitem.config.showAddIcon = true;
                            FSitem.config.onAddButtonClick = vm.AddADepartmentPopUp;
                        }else if(FSitem.name == 'designation_id'){
                            FSitem.config.showAddIcon = true;
                            FSitem.config.onAddButtonClick = vm.AddDesignationPopUp;
                        }else if(FSitem.name == "policy_id"){
                            FSitem.validationRules = {
                                "required": false
                            }
                        }else if(FSitem.name == 'date_format'){
                            FSitem.config.options = [{value:'M/d/yyyy',name:'M/D/YYYY'},{value:'dd/MM/yyyy',name:'DD/MM/YYYY'},{value:'MM/dd/yyyy',name:'MM/DD/YYYY'}]
                        }else if(FSitem.name == 'avatar'){
                            FSitem.config = {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    // {
                                    //     fileType: 'mp4', // Video types
                                    //     iconClass: 'fa-file-video-o' // Icon class for videos
                                    // },
                                    // {
                                    //     fileType: 'mov', // Video types
                                    //     iconClass: 'fa-file-video-o' // Icon class for videos
                                    // },
                                    // Add more image, document, text, and video types as needed
                                ],
                                multiple: false,
                            }
                            FSitem.config.onChange = vm.GetAttachement;
                        }
                    })
                    if(getSchemaValue[1] != undefined){vm.ShowAdvanceSetting = true;}
                    vm.isLoading = false;
                }else{
                    vm.FormSchema = [];
                    vm.FormSchemaAdvanceSettings = [];
                    vm.isLoading = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        GetAttachement(x, y, z){
            var vm = this;
            if(y.name == "avatar") {
                vm.GetBase64String(y.value);
            }
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                //alert(vm.apiurl);
                //vm.fileData = [];

                var f = evt[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        // vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        // var fileSize = f.size;
                        // vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        console.log(fileObj);
                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        SubmitUser(){
            var vm = this;
            vm.$refs.refAddUser.validateForm.validate().then(async valid =>{
                if(valid){
                    vm.getFormSchemaData = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    vm.getFormSchemaAdvanceSettings = vm.$refs.refAddUser.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchemaAdvanceSettings);
                    var tempjson = {
                        FirstName: vm.GetFormData("first_name"),
                        LastName: vm.GetFormData("last_name"),
                        EmailId:vm.GetFormData("email_id"),
                        Password:vm.GetFormData("password"),
                        NewPassword:vm.GetFormData("confirm_password"),
                        Gender:vm.GetFormData("gender"),
                        UserTypeId: vm.GetFormData("user_type_id"),
                        UserTitleId:vm.GetFormData("user_title_id"),
                        EnableLogin:vm.GetFormData("is_enable_login"),
                        TimezoneId:vm.GetFormData("timezone_id"),
                        DateFormat:vm.GetFormData("date_format"),
                        Avatar:vm.fileData.length > 0 ? vm.fileData : null,
                        RoleId:vm.GetFormData("role_id"),
                        ShiftId:vm.GetFormData("shift_id"),
                        HourlyRate:vm.GetFormData("hourly_rate"),
                        DepartmentId:vm.GetFormData("department_id"),
                        DesignationId:vm.GetFormData("designation_id"),
                        DateofBirth:vm.GetFormData("date_of_birth"),
                        DateofJoining:vm.GetFormData("date_of_join"),
                        DateofLeave:vm.GetFormData("date_of_leave"),
                        AliasName:vm.GetFormData("alias_name"),
                        IsVirtual:vm.GetFormData("is_virtual"),
                        PolicyId:vm.GetFormData("policy_id") == "" ? 0 : vm.GetFormData("policy_id"),
                    }
                    var getJson = JSON.stringify(tempjson);
                    DataService.AddUserData(getJson).then(response=>{
                        debugger
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            })
        },
        GetFormData(value){
            var vm = this;
            var checkifexistinformschemadata = vm.getFormSchemaData.find(x=> x.field_name == value);
            if(checkifexistinformschemadata != undefined){
                return checkifexistinformschemadata.field_value;
            }else{
                var checkifexistinformschemaadvancesettings = vm.getFormSchemaAdvanceSettings.find(x=> x.field_name == value);
                if(checkifexistinformschemaadvancesettings != undefined){
                    return checkifexistinformschemaadvancesettings.field_value;
                }
            }
        }
    }
}
</script>